import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable } from 'rxjs';
import { IEstadoVenta } from '../models/estado-ventas';

@Injectable()
export class EstadosVentasService {
    constructor(private http: HttpClient) {}
    /**
     * Funcion en el Servicio 
     * @param estado
     * @returns Un Estado de Venta Nuevo
     */
    saveEstadoVenta(estado: IEstadoVenta): Observable<IEstadoVenta> {
        return this.http.post<IEstadoVenta>(`${environment.administracion.estados_ventas}`, estado);
    }
    /**
     * Funcion en el servicio
     * @param estado 
     * @returns Estado Venta actualizado
     */
    updateEstadoVenta(estado: IEstadoVenta): Observable<IEstadoVenta> {
        return this.http.put<IEstadoVenta>(`${environment.administracion.estados_ventas}`+'/'+estado.idestado, estado);
    }
    /**
     * Funcion en el servicio
     * @param idestado
     * @returns Elimina un Estado Venta
     */
    deleteEstadoVenta(idestado: number): Observable<IEstadoVenta> {
        return this.http.delete<IEstadoVenta>(environment.administracion.estados_ventas+'/'+idestado);
    }
}